<template>
  <div class="container">
    <h3></h3>
    <form v-on:submit.prevent="submitForm" action="memberInsert">
      <div class="name">
        <label for="name"></label>
        <input type="text" v-model="userName" placeholder=" " required />
      </div>
      <hr />
      <div class="id">
        <label for="id"></label>
        <input type="text" v-model="userId" placeholder=" " required />
        <button type="button" @click="checkUserId"></button>
        <span v-if="userIdChecked && userIdExists">   .</span>
        <span v-if="userIdChecked && !userIdExists">  .</span>
      </div>
      <hr />
      <div class="pw">
        <label for="password"></label>
        <input type="password" v-model="userPw" placeholder=" " required />
        <label>//  2  ,10~16</label>
      </div>
      <div class="pwfirm">
        <label> </label>
        <input type="password" v-model="passwordConfirm" @change="pwMatchCheck" placeholder="  "
          required />
        <span id="pwCheckConfirm" :class="{ 'match': passwordsMatch, 'not-match': !passwordsMatch }"> {{
      passwordMatchMessage }}</span>
      </div>
      <hr />
      <div class="phone">
        <label></label>
        <select id="mobile1" v-model="mobile1" name="mobile" label="mobile">
          <option value="010">010</option>
          <option value="011">011</option>
          <option value="016">016</option>
          <option value="017">017</option>
          <option value="018">018</option>
          <option value="019">019</option>
        </select>
        -
        <input id="mobile2" v-model="mobile2" name="mobile[]" maxlength="4" fw-filter="isNumber&amp;isFill"
          fw-label="" fw-alone="N" placeholder="" value="" type="text" />
        -
        <input id="mobile3" v-model="mobile3" name="mobile[]" maxlength="4" fw-filter="isNumber&amp;isFill"
          fw-label="" fw-alone="N" placeholder="" value="" type="text" />
      </div>
      <hr />
      <div class="addressbox">
        <div class="address">
          <label></label>
          <input type="text" id="postcode" v-model="userPostCode" placeholder="" />
          <input type="button" style="width: 80px; height: 30px; font-weight: 300;" @click="search()"
            value="" /><br /><br />
          <input type="text" id="roadAddress" v-model="userRoadAddress" placeholder="" />
          <input type="text" id="jibunAddress" v-model="userJibunAddress" placeholder="" />
          <span id="guide" style="color: #000; display: none"></span>
          <input type="text" id="detailAddress" v-model="userDetailAddress" placeholder="" />
          <input type="text" id="extraAddress" v-model="userExtraAddress" placeholder="" />
        </div>
      </div>
      <h3></h3>
      <div class="checkbox">
        <div class="form-check">
          <input class="form-check-input" type="checkbox" v-model="allAgreed" @change="checkAllAgreement"
            id="flexCheckDefault" />
          <label class="form-check-label" for="flexCheckDefault">
              
          </label>
        </div>
        <hr />
        <div class="form-check" v-for="(agreement, index) in agreements" :key="index">
          <input class="form-check-input" type="checkbox" v-model="agreement.agreed" :id="'flexCheckChecked' + index" />
          <label class="form-check-label" :for="'flexCheckChecked' + index">
            {{ agreement.label }}
          </label>
        </div>
        <hr />
        <p>    .</p>
      </div>
      <button type="submit" class="btn" :disabled="!allAgreed"> </button>
    </form>
  </div>
</template>

<script>
import axios from 'axios'; // axios import .
import router from '@/router'; // Vue Router import .

const serverUrl = 'http://localhost:80'
export default {
  name: "AddressRoad",

  data() {
    return {
      userId: '',
      userPw: '',
      passwordConfirm: '',
      userPostCode: '',
      userName: '',
      userRoadAddress: '',
      userJibunAddress: '',
      userDetailAddress: '',
      userExtraAddress: '',
      mobile1: '',
      mobile2: '',
      mobile3: '',
      mobile: '',
      userIdChecked: true, //   
      userIdExists: false, //   
      agreements: [
        { label: '[]  ', agreed: false },
        { label: '[]     ', agreed: false },
        { label: '[]  ', agreed: false }
      ]
    };
  },
  computed: {
    passwordMatchMessage() {
      return this.passwordsMatch ? '.' : ' .';
    }
  },
  methods: {

    submitForm() {
      if (!this.userIdChecked || this.userIdExists) {
        alert("  .");
        return;
      }

      const postData = {
        userId: this.userId,
        userPw: this.userPw,
        userPostCode: this.userPostCode, //   
        userRoadAddress: this.userRoadAddress, //   
        userDetailAddress: this.userDetailAddress,
        userJibunAddress: this.userJibunAddress,
        userExtraAddress: this.userExtraAddress,
        userName: this.userName,
        userMobile: this.mobile1 + this.mobile2 + this.mobile3,
      }
      alert(this.userName + '  !!!')
      axios
        .post(serverUrl + '/member/memberInsert', postData) // axios   .
        .then(response => {
          console.log(response);
          alert(' .');
          router.push({ name: 'login' }); //     .
        })
        .catch(error => {
          alert(' ');
          console.error(error);
        });
      // alert(postData)
    },
    //  
    checkUserId() {
      if (this.userId) {
        axios.get(serverUrl + '/member/checkUserId', { params: { userId: this.userId } })
          .then(response => {
            this.userIdChecked = true;
            this.userIdExists = response.data.exists;
          })
          .catch(error => {
            console.error(error);
          });
      } else {
        alert(" .");
      }
    },
    search() {
      new window.daum.Postcode({
        oncomplete: (data) => {
          var roadAddr = data.roadAddress; //   
          var extraRoadAddr = ""; //   

          if (data.bname !== "" && /[||]$/g.test(data.bname)) {
            extraRoadAddr += data.bname;
          }
          //  ,   .
          if (data.buildingName !== "" && data.apartment === "Y") {
            extraRoadAddr +=
              extraRoadAddr !== ""
                ? ", " + data.buildingName
                : data.buildingName;
          }
          //    ,     .
          if (extraRoadAddr !== "") {
            extraRoadAddr = " (" + extraRoadAddr + ")";
          }

          //      .
          this.userPostCode = data.zonecode;
          this.userRoadAddress = roadAddr;
          this.userJibunAddress = data.jibunAddress;

          //       .
          if (roadAddr !== "") {
            this.userExtraAddress = extraRoadAddr;
          } else {
            this.userExtraAddress = "";
          }

          var guideTextBox = document.getElementById("guide");
          //  ' '  ,    .
          if (data.autoRoadAddress) {
            var expRoadAddr = data.autoRoadAddress + extraRoadAddr;
            guideTextBox.innerHTML = "(   : " + expRoadAddr + ")";
            guideTextBox.style.display = "block";
          } else if (data.autoJibunAddress) {
            var expJibunAddr = data.autoJibunAddress;
            guideTextBox.innerHTML = "(   : " + expJibunAddr + ")";
            guideTextBox.style.display = "block";
          } else {
            guideTextBox.innerHTML = "";
            guideTextBox.style.display = "none";
          }
        }
      }).open();
    },

    pwMatchCheck() {
      if (this.passwordConfirm == this.userPw && this.userPw != '') {
        document.getElementById("pwCheckConfirm").innerText = ".";
      }
      else {
        document.getElementById("pwCheckConfirm").innerText = " .";
      }
    },
    checkAllAgreement() {
      for (const agreement of this.agreements) {
        agreement.agreed = this.allAgreed;
      }
    }
  }
};
</script>


