<template>
  <div class="container">

    <div class="textGroup">
      <h2> </h2>
      <p>
              .
        <br />
                  
          .
      </p>
    </div>
    <div class="memberType">
      <p></p>
      <select class="form-select custom-form-control-secondary" aria-label="Default select example">
        <option selected></option>
        <option value="1"></option>
      </select>
    </div>



    <form @submit.prevent="findUserId">
      <div class="input-group mb-3">
        <p></p>
        <input type="text" class="form-control custom-form-control-secondary rounded" v-model="userName"
          aria-label="Username" required :disabled="isVerificationCodeVisible" />
      </div>
      <div class="call">
        <div class="callbox">
          <div class="one" v-if="callNumber == 'call'">
            <label for="phone"></label>
            <input type="tel" id="phone" v-model="userMobile" class="form-control custom-form-control-secondary"
              name="phone" placeholder=": 010-1234-5678" pattern="[0-9]{2,3}[0-9]{3,4}[0-9]{4}" required :disabled="isVerificationCodeVisible" />
          </div>

        </div>
      </div>
      <div id="verification" class="d-flex flex-column">
        <button class="code" @click.prevent="requestVerificationCode" :disabled="isVerificationCodeVisible"> </button>
        <div class="d-flex" v-if="isVerificationCodeVisible">
          <input class="borderbox" type="text" v-model="verificationCode" placeholder="">  
          <button class="btn btn-info btn-sm justify-content-center"></button>
        </div>
      </div>
      <button class="btn btn-outline-secondary" type="submit" id="button-addon1"> </button>
    </form>
