</template>



<script>

import axios from 'axios'; // axios import .
const serverUrl = 'http://localhost:80'

export default {
  data() {
    return {
      verificationCode: '',//        
      callNumber: 'call',
      userMobile: '',
      userName: '',
      isVerificationCodeVisible:false,
    };
  },
  methods: {
    requestVerificationCode() {
      //       .
      if(this.userName == "" || this.userMobile == ""){
        //       .
        alert("  ")
      }else{
        var data = {
        userName: this.userName,
        userMobile: this.userMobile

      }
      
      axios.post(serverUrl + '/member/checkInfo', data)
        .then(response => {
          if (response.data) {
            alert(' ')
            this.isVerificationCodeVisible = true;
          } else {
            alert('   .')
          }
        })
        .catch(error => {
          console.error(' ', error);
        })
    }
      }

      
    ,
    findUserId() {
      var data = {
        userName: this.userName,
        userMobile: this.userMobile

      }
      if (this.verificationCode) {

        //       
        axios.post(serverUrl + '/member/find-user-id', data)
          .then(response => {
            alert(` : ${response.data}`);
          })
          .catch(error => {
            console.error('Error finding user ID:', error);
          });
      } else {
        //      .
        alert(' .');
      }
    }
  }
};
</script>

