<template>
  <div class="container">
    <div class="main d-flex flex-column align-items-center">
      <h1 class="body-title-30norm pt-5"></h1>
      <section class="cartList d-flex flex-column align-items-start">
        <table class="table cartDetail text-nowrap table-group-divider align-items-center align-self-stretch">
          <thead>
            <tr>
              <th scope="col" class="headTag body-content-md text-center">
                
              </th>
              <th scope="col" class="headTag body-content-md"> </th>
              <th scope="col" class="headTag body-content-md text-center">
                
              </th>
              <th scope="col" class="headTag body-content-md"> </th>
              <th scope="col" class="headTag body-content-md"> </th>
              <th scope="col" class="headTag body-content-md"> </th>
              <th scope="col" class="headTag body-content-md"></th>
              <th scope="col" class="headTag body-content-md text-center">
                
              </th>
            </tr>
          </thead>
          <tbody class="table-group-divider align-middle">
            <tr>
              <td class="text-center">
                <img :src="this.cartList.dmstc.productList[0].src" alt=" " />
              </td>
              <td class="body-menu-md">
                {{ this.cartList.dmstc.productList[0].name }}
              </td>
              <td>
                <div class="btn-group-sm d-flex justify-content-center align-items-center">
                  <button class="btn btn-outline-secondary" v-on:click="quantityChange(-1)">
                    <img src="@/assets/symbol_minus.png" alt=" " />
                  </button>
                  <p style="margin: 0;" class="border border-outline-secondary text-center body-content-md">
                    {{ quantityCount }}
                  </p>
                  <button class="btn btn-outline-secondary" v-on:click="quantityChange(1)">
                    <img src="@/assets/symbol_plus.png" alt=" " />
                  </button>
                </div>
              </td>

              <td id="totalPrice" class="body-menu-md">{{ totalPrice }} </td>
              <td class="body-content-md">0 </td>
              <td class="body-content-md"></td>
              <td class="body-content-md">3,000 </td>
              <td class="d-flex flex-column gap-1">
                <button class="btn btn-outline-primary body-content-md">
                  
                </button>
                <button class="btn btn-outline-primary body-content-md">
                  
                </button>
                <button class="btn btn-outline-secondary body-content-md">
                   
                </button>
                <button class="btn btn-outline-warning body-content-md">
                  &times; 
                </button>
              </td>
            </tr>
          </tbody>
        </table>
      </section>

      <div class="selectsOption d-flex justify-content-between align-items-center align-self-stretch">
        <p class="body-content-md text-start" style="color: #28853d">
          *       
        </p>
        <div class="clearPrint d-flex align-items-center">
          <button class="btn btn-outline-secondary">
            <span class="body-content-norm"> </span>
          </button>
          <button class="btn btn-outline-secondary">
            <span class="body-content-norm"> </span>
          </button>
        </div>
      </div>
      <div class="totalSummary text-left">
        <section class="totalSummary-Header body-content-md d-flex justify-content-between w-100 gap-5">
          <p>  </p>
          <p> </p>
          <p>  </p>
          <p>  </p>
        </section>
        <section class="totalSummary-contents body-title-30norm d-flex justify-content-between w-100 gap-5">
          <p>{{ totalPrice }}<b class="body-title-norm"> </b></p>
          <p>+0<b class="body-title-norm"> </b></p>
          <p class="text-primary">= {{ totalPrice }}<b class="body-title-norm"> </b></p>
          <p class="body-title-norm"><b class="body-title-30norm text-warning"> 900 </b></p>
        </section>
      </div>
      <div class="payBtn d-flex justify-content-center align-items-start align-self-stretch">
        <button class="btn btn-outline-secondary"></button>
        <button class="btn btn-outline-primary"></button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ShoppingCart",
  components: {},
  mounted() {
    this.updateData();
  },
  data() {
    return {
      cartList: {
        dmstc: {
          count: 1,
          productList: [
            {
              src: require("@/assets/product_extra_small.jpg"),
              name: "[NEW]     ",
              quantity: 1,
              price: 19200,
              total: 19200,
            },
          ],
        },
        ovss: {
          count: 0,
        },
      },
      quantityCount: 0,
      totalPrice: 0,
    };
  },
  methods: {
    quantityChange(diff) {
      let amount = this.cartList.dmstc.productList[0].quantity + diff;
      if (amount < 1) {
        amount = 1;
      }
      this.cartList.dmstc.productList[0].total =
        amount * this.cartList.dmstc.productList[0].price;
      this.cartList.dmstc.productList[0].quantity = amount;
      this.updateData();
      return;
    },
    updateData() {
      this.quantityCount = this.cartList.dmstc.productList[0].quantity;
      this.totalPrice =
        this.cartList.dmstc.productList[0].total.toLocaleString("ko-KR");
      console.log(this.totalPrice);
      return;
    },
  },
};
</script>

<style scoped>
div.container>div.main {
  gap: 40px;
}

.main>section.cartList {
  gap: 20px;
}

div.deliverTypeMenu {
  width: 100%;
}

.cartList {
  width: 1200px;
}

.cartList>div.deliverTypeMenu>span {
  padding: 24px 40px;
}

div.btn-group-sm>p {
  width: 32px;
  height: 31px;
  line-height: 26px;
}

div.tableBody>div:last-child {
  gap: 4px;
}

div.tableBody>div:last-child>button:first-child {
  background-color: #28c852;
}

.main>p {
  width: 100%;
}

td#totalPrice {
  width: 120px;
}

div.selectsOption>div {
  gap: 20px;
}

div.selectsOption button {
  padding: 8px 16px;
}

div.totalSummary {
  width: 1200px;
}

div.totalSummary p,
div.totalSummary h3 {
  width: 200px;
}

div.payBtn {
  gap: 12px;
}
</style>
